.news-route {
  header {
    position: relative;
    width: 100%;
    height: 69px;
    background: url(../../assets/bg-wool-dark.png);
    background-position-x: right;

    box-shadow: 0 3px 5px -1px #00000099;

    img {
      position: absolute;
      max-width: 400px;
      top: 1rem;
      left: 50%;
      transform: translate(-50%);
    }
  }

  hr {
    width: 100%;
    border-color: hsl(0, 0%, 36%);
  }

  .container {
    width: 770px;
    margin: auto;
  }

  nav {
    display: flex;
    min-width: 100%;
    flex-direction: column;
    place-content: center;
    padding: 1.6rem 1rem 0 0;

    ul {
      list-style-type: none;
      display: flex;
    }

    li {
      padding: 0.2rem 1rem;
    }

    a {
      position: relative;
      color: white;
      text-decoration: none;
      font-size: 0.9rem;
      font-family: 'Noto Sans';
    }

    a.active {
      &::after {
        background: white;
        transition-property: width;
        transition-duration: 200ms;
        width: 20px;
      }
    }

    a::after {
      content: '';
      display: block;
      position: absolute;
      width: 0;
      height: 3px;
      background: transparent;
      left: 50%;
      transform: translate(-50%);
      bottom: -5px;
    }
  }

  .filter-form {
    display: flex;
    flex-direction: row;
    height: max-content;
    width: 100%;
    gap: 0 1rem;

    h3 {
      color: white;
      font-family: 'Noto Sans Bold';
      text-transform: uppercase;
      font-size: 11px;
      padding-bottom: 4px;
    }

    input[type="text"] {
      background: hsl(0, 0%, 5%);
      border: 1px solid hsl(0, 0%, 5%);
      height: 32px;
      color: white;
      width: 100%;
      caret-color: white;
      padding: 0 0.4rem;
    }
  }

  .divider {
    // width: 2px;
    background: hsl(0, 0%, 36%);
    height: 36px;
  }

  .form-side {
    width: 50%;
    padding: 1rem 0.4rem 0.4rem;
  }
}