$alert-border: #6D6D6E;
$alert-bg: #48494A;

.alert-message {
  display: flex;
  flex-direction: row;
  border-top: 1px solid $alert-border;
  background: $alert-bg;
  width: 100%;
  place-items: center;

  .icon {
    width: 22px;
    height: 22px;
    margin: 0 0.5rem 0 0.7rem;
    path {
      fill: white;
    }
  }

  .message {
    vertical-align: middle;
    width: 100%;
    padding: 0.4rem 2rem 0.5rem 0;
    color: white;
  }

  .close-btn {
    cursor: pointer;
    width: 36px;
    height: 100%;
    border: 1px solid transparent;
    background: none;

    &:hover,
    &:focus {
      outline: none;
      background: hsl(180, 1%, 36%);
    }
  }

  a {
    color: white;
  }

  .close-icon {
    width: 12px;
  }
}