.mainmenu-tab {
  width: 100%;
  height: 56px;
  min-height: 56px;
  transition-property: background;
  transition-duration: 180ms;

  &:hover {
    background: hsl(0, 0%, 24%);
  }

  a {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    display: flex;
    text-decoration: none;

    &:focus {
      outline: none;
    }

    &:focus-visible {
      border: 1px solid white;
      background: hsl(0, 0%, 24%);
    }
  }

  a.active {
    .mainmenu-tab-cont {
      font-family: 'Noto Sans Bold';
    }

    &::before {
      background: white;
      transition-property: height;
      transition-duration: 200ms;
      height: 18px;
    }
  }

  a::before {
    content: '';
    display: block;
    position: absolute;
    background: transparent;
    width: 4px;
    height: 0px;
    top: 50%;
    transform: translate(0, -50%);
  }

  .mainmenu-tab-icon {
    padding: 0rem 0.9rem;
    display: flex;
    height: 100%;
    place-items: center;
    place-content: center;
    padding: 0.8rem 0.85rem;

    img {
      width: auto;
      height: 29px;
      image-rendering: pixelated;
    }
  }

  .mainmenu-tab-cont {
    display: flex;
    flex-direction: column;
    place-content: center;
    width: 100%;
    font-size: 14px;
    line-height: 1rem;
    text-align: middle;
    margin-top: -0.1rem;
    color: white;
    font-family: 'Noto Sans';

    i {
      font-style: normal;
      text-transform: uppercase;
      width: 100%;
      font-size: 11px;
      color: white;
    }

    span {
      font-weight: 100;
      font-style: normal;
    }
  }
}